/*  Article Ask Derm Thumbnail
*
*  Version:
*    1.0.0 - 2021/12/23
*/

.o-article-ask-derm-thumbnail {
  width: 15.125rem;
  padding: 1.5rem;
  background-color: $s-color--background;

  &__image-container {
    padding-bottom: 75.71%;
  }

  &__pretitle {
    margin-top: 1.3125rem;
  }

  &__title {
    display: block;
    @include t-font--type-base;
    margin-top: 1.3125rem;
  }

  &__text {
    margin-top: 0.4375rem;
    max-width: 30.6875rem;
    width: 100%;
    line-height: 1rem;
  }

  &__btn {
    @include t-font--type-7;
    text-transform: capitalize;
    margin-top: 0.8125rem;

    &.o-link.has-arrow {
      background-position-y: 0.3125rem;
    }
  }

  &__comments {
    margin-top: 0.8125rem;
    display: flex;
    align-items: center;

    &-text {
      display: none;
    }

    &-icon {
      margin-left: 0.25rem;
      width: 1rem;
      height: 1rem;
    }
  }

  @include t-media-query($s-medium-up) {
    display: flex;
    padding: 2rem;
    max-width: 47.25rem;

    &__image-container {
      width: 25%;
      padding-bottom: 36.71%;
      margin-right: 1.25rem;
    }

    &__pretitle {
      margin-top: 0.4375rem;
    }

    &__title {
      margin-top: 1.9375rem;
    }

    &__text {
      margin-top: 0.5625rem;
      line-height: 1.4375rem;
    }

    &__comments {
      margin-top: 3.0625rem;

      &-icon {
        margin-left: 0.25rem;
        width: 1.25rem;
        height: 1.25rem;
      }

      &-text {
        display: block;
        @include t-font--type-5;
        margin-left: 0.5rem
      }
    }
  }
}