/*  Ask a Derm Slider
*
*  Version:
*    1.0.0 - 2022/01/20
*/

.c-ask-derm {
  &__articles {
    &-slider {
      background-color: $s-color--background-tertiary;
    }

    &-inner {
      padding: 4.1875rem 0 4.3125rem 2.4375rem;
    }

    &-title {
      margin-bottom: 1.6875rem;
    }
  }

  @include t-media-query($s-medium-up) {
    &__articles {

      &-inner {
        padding: 8.375rem 0 9.875rem 8.75rem;
      }

      &-title {
        margin-bottom: 4.6875rem;
      }
    }
  }
}